/* ==========================================================================
   Team shortcode style - begin
   ========================================================================== */

.edgtf-team-holder {
    @include edgtfRelativeHolderLayout();
	
	&.edgtf-team-info-on-image {
		
		.touch & {
			cursor: pointer;
		}

		&:hover {
			
			.edgtf-team-social-wrapper {
				opacity: 1;
			}

			.edgtf-team-social-inner {
				@include edgtfTransitionTransform(.45s cubic-bezier(0.64, 0.01, 0.15, 1.16), opacity .5s);
				@include edgtfTransform(translate3d(0,0,0));
			}
		}
		
		.edgtf-team-social-holder {
			margin: 7px 0 0;
		}
	}

    .edgtf-team-image {
        @include edgtfRelativeHolderLayout();

        img {
            display: block;
        }
    }
	
	.edgtf-team-info {
		@include edgtfRelativeHolderLayout();
		margin: 27px 0 0;
	}

    .edgtf-team-name {
        margin: 0;
    }

    .edgtf-team-position {
        margin: 7px 0 0;
        font-size: 18px;
        display: block;
    }

    .edgtf-separator-holder {

        text-align: inherit;

        .edgtf-separator {
            border-color: $first-main-color;
            width: 138px;
            border-bottom-width: 7px;
            margin: 26px 0 10px;
        }
    }
	
	.edgtf-team-text {
		margin: 10px 0 0;
	}
	
	.edgtf-team-social-wrapper {
		@include edgtfAbsoluteHolderLayout();
		background-color: rgba(#fff, .85);
		z-index: 1;
		opacity: 0;
		@include edgtfTransform(translateZ(0));
		@include edgtfTransition(opacity .3s);
	}
	
	.edgtf-team-social-outer {
		@include edgtfTableLayout();
	}
	
	.edgtf-team-social-inner {
		position: relative;
		display: table-cell;
		height: 100%;
		width: 100%;
		padding: 20px 40px 33px;
		vertical-align: bottom;
		@include edgtfTransitionTransform(.2s ease);
		@include edgtfTransform(translate3d(0,40px,0));
	}
	
	.edgtf-team-social-holder {
		@include edgtfRelativeHolderLayout();
		margin: 17px 0 0;
		
		.edgtf-team-icon {
			font-size: 23px;
			margin: 0 25px 0 0;

			.edgtf-icon-element {
				font-size: inherit;
                color: #fff;
				@include edgtfTransition(text-shadow 0.15s ease-out);
                @include edgtfFontShadow($first-main-color);
                &:hover {
                    @include edgtfFontShadowHover($first-main-color);
                }
			}
		}
	}

    &.edgtf-dark-skin {
        .edgtf-team-social-holder .edgtf-team-icon .edgtf-icon-element {
            color: $light-text-color
        }
    }
}
/* ==========================================================================
   Team shortcode style - end
   ========================================================================== */